import Stream from './stream';

/**
 *  `Client.on('stream-added')` <br>
 * <br>
 * <br>
 *  {@link RemoteStream#getType getType()} <br>
 * <br>
 *  `Client.on('stream-added')`  `Client.on('stream-removed')` 
 * `Client.on('stream-updated')`  {@link LocalStream#addTrack addTrack()}  {@link LocalStream#removeTrack removeTrack()}
 *  track 
 *
 * @class RemoteStream
 * @extends {Stream}
 * @example
 * // 
 *
 * // stream-added
 * const client = TRTC.createClient({ sdkAppId, userId, userSig, mode: 'live'});
 * client.on('stream-added', event => {
 *   const remoteStream = event.stream;
 *   const remoteUserId = remoteStream.getUserId();
 *   console.log('received a remoteStream ID: ' + remoteStream.getId() + ' from user: ' + remoteUserId);
 *   // 
 *   client.subscribe(remoteStream);
 *
 *   // SDK
 *   // client.unsubscribe(remoteStream);
 * });
 *
 * // stream-removed
 * client.on('stream-removed', event => {
 *   const remoteStream = event.stream;
 *   console.log('remoteStream ID: ' + remoteStream.getId() + ' has been removed');
 *   // <video>
 * });
 *
 * // stream-updated
 * client.on('stream-updated', event => {
 *   const remoteStream = event.stream;
 *   console.log('remoteStream ID: ' + remoteStream.getId() + ' was updated hasAudio: '
 *               + remoteStream.hasAudio() + ' hasVideo: ' + remoteStream.hasVideo());
 * });
 *
 * // stream-subscribed
 * client.on('stream-subscribed', event => {
 *   const remoteStream = event.stream;
 *   // HTML<video>IDremote-video-view
 *   // 
 *   remoteStream.play('remote-video-view');
 * });
 */
class RemoteStream extends Stream {
  /**
   * Creates an instance of RemoteStream.
   * @package
   * @param {object} options
   * @param {string} options.userId the remote userId who initiated the downlink connection
   * @memberof RemoteStream
   */
  constructor(options) {
    const extra = { isRemote: true, type: options.type };
    const opt = { ...options, ...extra };
    super(opt);
    this.isInSubscriptionCycle_ = false;
  }

  /**
   * <br>
   * <br>
   * 
   *
   * @returns {string} 
   * - 'main' 
   * - auxiliary 
   * @memberof RemoteStream
   */
  getType() {
    return super.getType();
  }

  setInSubscriptionCycle(subscribing) {
    this.isInSubscriptionCycle_ = subscribing;
  }

  isInSubscriptionCycle() {
    return this.isInSubscriptionCycle_;
  }
}

export default RemoteStream;
