/**
 * ()
 * stream
 * @returns {Promise<boolean>}
 */
async function initLocalStream() {
  let micId;
  const devices = await TRTC.getMicrophones();
  if (devices.length > 0) {
    micId = devices[0].deviceId;
    if (micId.length === 0) {
      micId = 'default';
    }
    console.log(`select microphone id:${micId}, label:${devices[0].label}`);
  } else {
    console.log('NO microphone found!');
  }
  WebGMEAPI.Param.micId_ = micId;
  if (!WebGMEAPI.Param.micId_) {
    return false;
  }
  log.info('start create local stream');
  WebGMEAPI.Param.localStream_ = TRTC.createStream({
    audio: true,
    video: false,
    userId: WebGMEAPI.Param.openId,
    cameraId: 0,
    microphoneId: micId
  });
  ReportSelfVersion(WebGMEAPI.Param.sdkAppId);
  // try {
  console.log('start init local stream!');
  // initialize the local stream and the stream will be populated with audio/video
  await WebGMEAPI.Param.localStream_.initialize();
  console.log('local stream init ok!');

  WebGMEAPI.Param.localStream_.on('player-state-changed', event => {
    log.info(`local stream ${event.type} player is ${event.state}`);
  });

  addView('local-stream');
  // eslint-disable-next-line no-undef
  const container = $('#' + 'local-stream')[0];
  // console.log('container:'+container);

  // play the localStream within 'local-stream' div
  await WebGMEAPI.Param.localStream_.play(container);

  console.log('start localstream play ok');
  console.log('start publish!');
  notifyEveny(WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_LOCALSTREAM_READY, 0, '');
  // publish the local stream
  await WebGMEAPI.Param.client_.publish(WebGMEAPI.Param.localStream_);
  log.info('start publish local stream ok!');

  setTimeout(getStatsLooper, WebGMEAPI.Param.interval_ || 1000);

  // log.error('start public local stream failed:'+e);

  //     notifyEveny(WebGMEAPI.fn.event.ITMG_MAIN_EVNET_TYPE_LOCALSTREAM_READY, 1, "");
  //     console.error('start localstream play error:'+e);
  //   });

  return true;
}

