/**
 * GMESDK
 * @param document htmldocument
 * @param sdkAppId sdkappId
 * @param openId sdkopenId
 */
WebGMEAPI.fn.Init = function(document, sdkAppId, openId) {
  WebGMEAPI.Param.document = document;
  WebGMEAPI.Param.sdkAppId = sdkAppId;
  WebGMEAPI.Param.openId = openId;
  log.info('sdkappid:' + sdkAppId);
};

function getStatsLooper() {
  if (WebGMEAPI.Param.client_) {
    let data = {};
    WebGMEAPI.Param.client_
      .getLocalAudioStats()
      .then(stats => {
        for (let userId in stats) {
          data.local_userId = userId;
          data.local_bytesSent = stats[userId].bytesSent;
          data.local_packetsSent = stats[userId].packetsSent;
          data.UploadBRSend = stats[userId].bytesSent;
          data.UploadRTT = 0;
          // console.log('$$$local userId: ' + data.local_userId +
          //             ' bytesSent: ' + data.local_bytesSent +
          //             ' packetsSent: ' + data.local_packetsSent);
        }

        let peerInfo = [];
        WebGMEAPI.Param.client_
          .getRemoteAudioStats()
          .then(stats => {
            for (let userId in stats) {
              let p = {};
              p.userId = userId;
              p.bytesReceived = stats[userId].bytesReceived;
              p.packetsReceived = stats[userId].packetsReceived;
              p.packetsLost = stats[userId].packetsLost;
              p.jitter = stats[userId].jitter;
              // p.delay = 0;
              // p.jitterBufferMs = 0;
              // p.jitterReceived = 0;
              peerInfo.push(p);
              // console.log('remote userId: ' + p.userId +
              //             ' bytesReceived: ' + p.bytesReceived +
              //             ' packetsReceived: ' + p.packetsReceived +
              //             ' packetsLost: ' + p.packetsLost);
            }
            data.PeerInfo = peerInfo;
            notifyEvenyObj(WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_USER_UPDATE, data);
            setTimeout(getStatsLooper, WebGMEAPI.Param.interval_ || 1000);
            // console.log('====local userId: ' + data.local_userId +
            //             ' bytesSent: ' + data.local_bytesSent +
            //             ' packetsSent: ' + data.local_packetsSent);
          })
          .catch(e => {
            console.error('getRemoteAudioStats error:' + e);
          });
      })
      .catch(e => {
        console.error('getLocalAudioStats error:' + e);
      });
  }
}

