/**
 * logINFO, TRACE, DEBUG, INFO, WARN, ERROR, NONE
 */
WebGMEAPI.fn.SetLogLevel = function SetLogLevel(level) {
  var logLevel = TRTC.Logger.LogLevel.INFO;
  switch (level) {
    case 0:
      logLevel = TRTC.Logger.LogLevel.TRACE;
      break;
    case 1:
      logLevel = TRTC.Logger.LogLevel.DEBUG;
      break;
    case 2:
      logLevel = TRTC.Logger.LogLevel.INFO;
      break;
    case 3:
      logLevel = TRTC.Logger.LogLevel.WARN;
      break;
    case 4:
      logLevel = TRTC.Logger.LogLevel.ERROR;
      break;
    case 5:
      logLevel = TRTC.Logger.LogLevel.NONE;
      break;
  }

  TRTC.Logger.setLogLevel(logLevel);
};

