// eventId used to show the events on avmonitor sender/receiver details page
const EventId = {
  PUBLISH_VIDEO: 0x8000, // 
  PUBLISH_AUDIO: 0x8001, // 
  UNPUBLISH_VIDEO: 0x8002, // 
  UNPUBLISH_AUDIO: 0x8003, // 
  MUTE_AUDIO: 0x8004, //  ()
  MUTE_VIDEO: 0x8005, //  ()
  UNMUTE_AUDIO: 0x8006, // 
  UNMUTE_VIDEO: 0x8007, // 
  SUBSCRIBE_VIDEO: 0x8008, // 
  SUBSCRIBE_AUDIO: 0x8009, // 
  UNSUBSCRIBE_VIDEO: 0x800a, // 
  UNSUBSCRIBE_AUDIO: 0x800b, // 
  SWITCH_CAMERA: 0x800c, // 
  SWITCH_MICROPHONE: 0x800d, // 
  REPLACE_VIDEO: 0x800e, // 
  REPLACE_AUDIO: 0x800f, // 
  MUTE_REMOTE_VIDEO: 0x8010, // 
  MUTE_REMOTE_AUDIO: 0x8011, // 
  UNMUTE_REMOTE_VIDEO: 0x8012, // 
  UNMUTE_REMOTE_AUDIO: 0x8013, // 
  JOIN: 0x8014, // 
  LEAVE: 0x8015, // 
  SIGNAL_DISCONNECTED: 0x8016, // 
  SIGNAL_CONNECTED: 0x8017, // 
  TRANSPORT_DISCONNECTED: 0x8018, // 
  TRANSPORT_CONNECTED: 0x8019 // 
};

const eventHub = new Map();

// add events to the hub
const addEvent = function(userId, event) {
  let list = eventHub.get(userId);
  if (!list) {
    eventHub.set(userId, []);
    list = eventHub.get(userId);
  }
  list.push(event);
};

// get the events and clear them in the hub
const getEvents = function(userId) {
  let list = eventHub.get(userId);
  if (!list) {
    list = [];
  } else {
    eventHub.delete(userId);
  }
  return list;
};

export { addEvent, getEvents, EventId };
